@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: linear-gradient(
    92.23deg,
    #45e08b 21.43%,
    #3bbd80 50.63%,
    #2d916c 100%,
    #3dc77b 117.04%
  );
  box-shadow: 0px 4.42184px 107.23px rgba(86, 255, 252, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #2d916c 21.43%,
    #3bbd80 50.63%,
    #45e08b 100%,

    #3dc77b 117.04%
  );
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #45e08b 21.43%,
    #3bbd80 50.63%,
    #2f9867 100%,
    #3cc279 117.04%
  );
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

.placeholder-con {
  background: linear-gradient(
    92.23deg,
    #45e08b 21.43%,
    #3bbd80 50.63%,
    #2d916c 100%,
    #3dc77b 117.04%
  );
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#3dc77b] to-[#F26C42] font-bold font-primary hover:from-[#FF738B] hover:to-[#3dc77b];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
